<template>
  <div class="card flex">
    <div class="col1 flex-auto">
      <div class="price"><span></span>{{card.price || currency}}</div>
      <div class="limit">{{card.limit}}</div>
      <div class="expired"> {{card.expiredStart}}  {{card.expiredStart}}</div>
    </div>
    <div v-if="card.use" class="col2"></div>
    <div v-else class="col2" @click="card.use = true"></div>
  </div>
</template>

<script>
  export default {
    props: {
      card: {
        required: true,
        type: Object
      }
    }
  };
</script>

<style lang="less" scoped>
  .card {
    background-color: #fff8f5;
    padding: 30px;
    background: url('~assets/goods/coupon.png') no-repeat;
    background-size: 100% 100%;
    color: #cdb49b;
    margin-top: 30px;
    .col1 {
      .price {
        font-size: 60px;
        span {
          font-size: 36px;
        }
      }
      .limit,
      .expired {
        font-size: 20px;
      }
    }
    .col2 {
      width: 170px;
      flex-shrink: 0;
      text-align: center;
    }
  }
</style>





